import React, { useState, useEffect } from 'react';

import axios from 'axios';

import './PageHome.css';
import Zoom from 'react-reveal/Zoom';


function PageHome(props) {

	const [main, setMain] = useState([]);
	useEffect(() => {
		axios({
			method: 'GET',
			url: 'http://192.168.0.10:8000/api/main'
		}).then((response) => {
			setMain(response.data);
		});
	}, []);


	const [postMain, setPostMain] = useState([])

	useEffect(() => {
		axios({
			method: "GET",
			url: `http://192.168.0.10:8000/api/blogpost/`
		}).then(response => {
			setPostMain(response.data)
		})
	}, [])



	return (
		<Zoom>
			<div className="PageHome">
				{main.map((c) => (c.id === 3)?(<h3>{c.name}</h3>) : null)}
				<br />
				{postMain.map((c) => (c.blog_category.id === 3) ? (<div><h3>{c.title}</h3>{c.content} <br /> < br /> </div>) : null)}
			</div>
		</Zoom>
	);
}

export default PageHome;
