import React, { useState, useEffect } from 'react';

import axios from 'axios';

import './PageTechnologies.css';
import Zoom from 'react-reveal/Zoom';

function PageTechnologies(props) {


		const [technologies, setTechnologies] = useState([]);
		useEffect(() => {
			axios({
				method: 'GET',
				url: 'http://192.168.0.10:8000/api/main'
			}).then((response) => {
				setTechnologies(response.data);
			});
		}, []);


	return (
		<Zoom>
			<div className="PageTechnologies">
				{technologies.map((c) => (c.id === 6)?(<h3>{c.name}</h3>) : null)}
				<br />
				Весь комплекс технических средств автоматизации, которые предлагает установить наша компания для
				обеспечения это состояние позволит иметь возможность жить в атмосфере красоты, уюта и высокого уровня
				функциональности, заниматься самосовершенствованием своей личности, не теряя время на решение
				элементарных вопросов надежности систем жизнеобеспечения (мультимедиа, вода, электричество, газ, тепло,
				климат канализация, водоснабжение и прочее) и предоставлять такую же возможность родным и близким.
				<br />
				Предлагаемые нашей компанией технические решения основываются на высоко технологичном и надежном
				оборудовании американских компаний: Lutron, Crestron и европейском оборудовании KNX/EIB, которые
				объединяют в единый интерфейс управление всеми инженерными системами дома. В качестве интерфейсов
				управления системой автоматизации предлагается широкая гамма различных панелей и пультов управления. Они
				могут быть настольными, переносными, встраиваться в стену. В качестве панелей управления могут быть
				использованы как оригинальные беспроводные панели компании Crestron, так и планшетные компьютеры Apple
				iPad в кейсах Sonance или iPhone.
			</div>
		</Zoom>
	);
}

export default PageTechnologies;
